@import "./variables";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  // this defines what 1 rem is
  font-size: 62.5%; /* calc(10 / 16 * 100)%*/
  font-family: "EB Garamond", serif;
  color: $mainTextColor;
  @media only screen and (min-width: 68.75em) {
    html {
      font-size: 60%;
    }
  }
}

body {
  background: url("styles/images/textured.jpeg") no-repeat no-repeat fixed
    center;
  box-sizing: border-box;

  min-height: calc(100vh - 2 * 4vw); // ?????
  .main {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    p {
      font-size: 2rem;
    }
    // h1{
    //   font-size: 4rem;
    // }
    // h3{font-size: 3rem;}
    main {
      flex: 1;
      hr {
        margin-top: 1rem;
        margin-bottom: 3rem;
        border: 0;
        height: 2px;
        background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0),
          rgba(0, 0, 0, 0.75),
          rgba(0, 0, 0, 0)
        );
      }
    }
  }
  footer {
    text-align: center;
    height: 7rem;
    background-color: $footerBackground;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    font-style: italic;
  }
}
